<template>
    <div class="content">
        <img :src="image.mt" class="img">
        <div class="boxs">
            <div class="box">
                <div class="hongbao">
                    <div class="hongbao-left">
                        <div class="hongbao-left-son">
                            <div class="hongbao-left-text"><font class="hongbao-left-text2" v-if="!showMoney">?</font><font class="hongbao-left-text1" v-if="showMoney">￥</font><font class="hongbao-left-text2" v-if="showMoney">0</font><font class="hongbao-left-text1" v-if="showMoney">.{{moneyNum}}</font></div>
                        </div>
                    </div>
                    <div class="hongbao-right">
                        <div class="hongbao-right-son1">
                            <font class="hongbao-right-text1">通用红包</font>
                            <br>
                            <font class="hongbao-right-text2">消费满35元 即可使用</font>
                        </div>
                        <div class="hongbao-right-son2">
                            赚现金
                        </div>
                    </div>
                </div>
                <div class="desc">
                    登录 美团APP 即可使用
                </div>
                <div @click="randomMoney" class="btn" :data-clipboard-text="code">
                    <template v-if="!showMoney">领取红包</template>
                    <template v-else>已领取</template>
                </div>
            </div>
            <div class="box">
                <div class="box-title">
                    <del>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</del><div>活动规则</div><del>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</del>
                </div>
                <div class="box-text-ui">1. 下单即可分享给小伙伴</div>
                <div class="box-text-ui">2. 每个链接仅限20人领取</div>
                <div class="box-text-ui">3. 每个链接会随机发放三个大额红包</div>
                <div class="box-text-ui">4. 每人每天仅限领取1次</div>
                <div class="box-text-ui">5. 红包仅限在线支付使用，且收餐人手机号、领取红包时输入的手机号和账号中的手机号需为同一手机号</div>
                <div class="box-text-ui">6. 其他未尽事宜，请咨询客服</div>
            </div>
        </div>



    </div>
</template>

<script>
    import Cookies from 'js-cookie'
    import Clipboard from 'clipboard'
    export default {
        data() {
            return {
                image: {
                    mt: require("../assets/mt.png")
                },
                code:"",
                showMoney:false,
                moneyNum:"4"
            }
        },
        mounted() {
            
            let Base64 = require('js-base64').Base64
            this.code=Base64.decode('WHVwYzY2MDNlRQ==')
            let clipboard = new Clipboard('.btn')
            let nowDate = new Date().toLocaleString().split(" ")[0]
            let cookieDate = Cookies.get("DATE2")
            if (nowDate == cookieDate) {
                this.moneyNum= Cookies.get("MONEY2")
                this.showMoney = true
            } else {
                this.moneyNum=(new Date().getTime())%3+4+""
                this.showMoney = false
            }
        },
        methods: {
            randomMoney() {
                if (!this.showMoney) {
                    this.showMoney = true
                    Cookies.set("DATE2", new Date().toLocaleString().split(" ")[0], { expires: 1 })
                    Cookies.set("MONEY2", this.moneyNum, { expires: 1 })
                }
            }
        }
    }
</script>

<style>
    .content {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        max-width: 500px;
        background: #893031;
        min-height: 100vh;
    }

    .img {
        width: 100%;
    }

    .boxs {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        width: 100%;
        padding: 0px 10px 50px 10px;
        box-sizing: border-box
    }

    .box {
        margin-top: 15px;
        display: flex;
        border-radius: 4px;
        flex-direction: column;
        align-items: center;
        width: 100%;
        background: #d0abac;
        padding: 10px;
        box-sizing: border-box;
    }

    .hongbao {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        height: 70px;
        box-sizing: border-box;
    }

    .hongbao-left {
        border-radius: 3px 0px 0px 3px;
        background: #fff;
        width: 25%;
        height: 95%;
        padding: 4px 0px 4px 4px;
        box-sizing: border-box;
    }

    .hongbao-left-son {
        border-radius: 3px 0px 0px 3px;
        border: 1px solid #E4E7ED;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .hongbao-left-text {
        width: 100%;
        height: 90%;
        color: #ea3248;
        display: flex;
        justify-content: center;
        align-items: baseline;
        box-sizing: border-box;
        /* background: #606266; */
    }

    .hongbao-left-text1 {
        font-size: 1.3rem;
    }

    .hongbao-left-text2 {
        font-size: 2.2rem;
    }

    .hongbao-right {
        border-radius: 2px 3px 3px 2px;
        background: #eb3022;
        width: 75%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .hongbao-right-son1 {
        width: 50%;
        height: 65%;
        color: #fff;
    }

    .hongbao-right-text1 {
        font-size: 0.9rem;
        font-weight: 600;
    }

    .hongbao-right-text2 {
        color: #f8f8f8;
        font-size: 0.7rem;
    }

    .hongbao-right-son2 {
        margin-left: 20px;
        width: 54px;
        height: 26px;
        background: #fdde83;
        border-radius: 13px;
        color: #606266;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;
    }

    .desc {
        margin-top: 10px;
        color: #4e361d;
        font-size: 0.8rem;
    }

    .btn {
        margin-top: 10px;
        border-radius: 4px;
        width: 100%;
        background: #ff3f26;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 1.2rem;
        line-height: 2.8rem;
        font-weight: 500;
    }

    .box-title{
        width: 100%;
        display: flex;
        justify-content: space-between;
        color:#b99794;
        font-size: 0.9rem;
        line-height: 2rem;
        margin-bottom: 7px;
        
    }
    .box-title div{
        font-size: 1.0rem;
        color: #333333;
    }

    .box-text-ui{
        color: #444444;
        width: 97%;
        font-size: 0.9rem;
        margin-bottom: 7px;
    }
</style>


